*{
  padding:0;
  margin:0;
  box-sizing:border-box;
}
.content{
  min-height: 100vh;
}
.navbar{
  background-color: rgb(23, 19, 85) !important;
}
.register-page{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}


/* // transcationp page */

.filters{
display: flex;justify-content: space-between;
align-items: center;
padding: 15px 20px;
box-shadow: 0 0 3px grey;
}
/* anticons */
.switch-icon{
  box-shadow: 0 0 15px rgb(0, 0, 0.677);
  border-radius: 5px;
  padding: 10px 15px; 
}

.anticon svg{
  font-size: 20px;
  cursor: pointer;
}
.active-icon{
  color: black;
}
.inactive-icon{
  color: grey;
}